@import '../variables';

.sort {
  position: relative;
  align-self: flex-start;

  &__label {
    display: flex;
    align-items: center;

    svg {
      margin-right: 8px;
      margin-top: 2px;
      transform: rotate(180deg);
      transition: .1s linear;

      &.open {
        transform: rotate(0deg);
      }

    }



    b {
      margin-right: 8px;

      @media (max-width:380px) {
        font-size: 14px;
      }
    }

    span {
      color: $orange;
      border-bottom: 1px dashed $orange;
      cursor: pointer;

      @media (max-width:380px) {
        font-size: 14px;
      }
    }
  }

  &__popup {
    position: absolute;
    right: 0;
    margin-top: 15px;
    background: #ffffff;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
    border-radius: 10px;
    overflow: hidden;
    padding: 10px 0;
    width: 180px;

    ul {
      overflow: hidden;

      li {
        padding: 12px 20px;
        cursor: pointer;

        &.active,
        &:hover {
          background: rgba(254, 95, 30, 0.05);
        }

        &.active {
          font-weight: bold;
          color: $orange;
        }
      }
    }
  }
}